import React from 'react';
import {connect} from "react-redux";
import {updateAppSettings} from "../../actions/foundationActions";
import Nav from "react-bootstrap/Nav";

const TestingNav = (props) => {
    return [
        <Nav.Item key="visualization" title="Visualizations"><Nav.Link onClick={()=>props.updateAppSettings({welcomeModName:"components/visualizations/VisualizationReboot"})} id="visualizationLink">V</Nav.Link></Nav.Item>,
        <Nav.Item key="randomLoad" title="Random Load"><Nav.Link onClick={()=>props.updateAppSettings({welcomeModName:"components/Testing/RandomLoad"})} id="randomLoadLink">Random Load</Nav.Link></Nav.Item>
        ]
}

const mapStateToProps = (state) =>{
    return {
        appSettings:state.appSettings,
        user:state.user
    };
};

export default connect(mapStateToProps,{
    updateAppSettings:updateAppSettings
})(TestingNav);
